export default {
  data: () => ({
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        value: '_id',
      },
      {
        text: 'หนังสือ',
        value: 'bookTitle',
      },
      {
        text: 'ผู้ยืม',
        value: 'fullName',
      },
      {
        text: 'วันที่ยืม',
        value: 'start_date',
      },
      {
        text: 'วันกำหนดคืน',
        value: 'end_date',
      },
      {
        text: 'วันคืน',
        value: 'returned_date',
      },
      {
        text: 'ค่าปรับ',
        value: 'fine',
        sortable: false,
      },
    ],
  }),
  computed: {
    transactions() {
      const state = this.$store.state.transaction.transactions
      const data =
        state.length > 0
          ? state.map((_transaction) => {
              const transaction = _transaction._doc
              if (!transaction) {
                return {
                  _id: '',
                  bookTitle: '',
                  fullName: '',
                  start_date: '',
                  end_date: '',
                  returned_date: '',
                  fine: '',
                }
              }
              transaction.bookTitle = `${transaction?.book?.title} (${transaction?.book?._id})`
              transaction.fullName = `${transaction.user.first_name} ${transaction.user.last_name}`
              transaction.fine = _transaction.fine ? _transaction.fine : 0
              return transaction
            })
          : []

      return data
    },
  },
  async created() {
    await this.$store.dispatch('transaction/fetchAllTransactions')
  },
}
